import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';

import { FormsModule } from '@angular/forms';
import { BLE } from '@ionic-native/ble';
import { BluetoothSerial } from '@ionic-native/bluetooth-serial';
import { ChartsModule } from 'ng2-charts';


/* COMPONENT */
import { MyApp } from './app.component';
import { NumberPickerComponent } from '../components/number-picker/number-picker';
import { LightsChartComponent } from '../components/lights-chart/lights-chart';

import { BleOperatorPageModule } from '../pages/ble-operator/ble-operator.module';
/* PAGES */
import { DevMode } from '../pages/dev-mode/dev-mode';
import { ModeManual } from '../pages/mode-manual/mode-manual';
import { ModeSchedulePage } from '../pages/mode-schedule/mode-schedule';


import { ContactPage } from '../pages/contact/contact';

import { TabsPage } from '../pages/tabs/tabs';


/* IONIC NATIVE */
import { WheelSelector } from '@ionic-native/wheel-selector';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { NativeStorage } from '@ionic-native/native-storage';

/* PROVIDERS */
import { LightsInfoProvider } from '../providers/lights-info/lights-info';
import { LightsGoupsProvider } from '../providers/lights-goups/lights-goups';

class WheelSelectorBrowser extends WheelSelector {

}

@NgModule({
  declarations: [
    NumberPickerComponent,
    LightsChartComponent,

    ModeSchedulePage,
    ModeManual,
    DevMode,

    MyApp,
    ContactPage,
    TabsPage,

  ],
  imports: [
    ChartsModule,

    FormsModule,
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/ble-operator/ble-operator.module#BleOperatorPageModule', name: 'BleOperatorPage', segment: 'ble-operator', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mode-manual/mode-manual.module#ModeManualModule', name: 'ModeManual', segment: 'mode-manual', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mode-schedule/mode-schedule.module#ModeSchedulePageModule', name: 'ModeSchedulePage', segment: 'mode-schedule', priority: 'low', defaultHistory: [] }
  ]
}),
    

    BleOperatorPageModule

  ],
  bootstrap: [IonicApp],
  entryComponents: [
    ModeSchedulePage,
    ModeManual,
    DevMode,

    MyApp,
    ContactPage,
    TabsPage
  ],
  providers: [
    BLE,
    BluetoothSerial,
    NativeStorage,
    { provide: WheelSelector, useClass: WheelSelectorBrowser},
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    LightsInfoProvider,
    LightsGoupsProvider
    
  ]
})
export class AppModule {}



